body {
    font-family: Arial;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* Style the search field */
.example input[type=text] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
}

/* Style the submit button */
.example button {
  float: left;
  width: 20%;
  padding: 10px;
  background: #2196F3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

.example button:hover {
  background: #0b7dda;
}

/* Clear floats */
.example::after {
  content: "";
  clear: both;
  display: table;
}

#noofpg {
 width:5%;
 margin: 2.5% 5% 2.5% 5%;
}

html {
  background: url(https://i.imgur.com/AMf9X7E.jpg) no-repeat center center fixed;
  background-size: cover;
}

.example {
  float:left;
  width: 100%;
  margin-bottom: 5%;
}

.search {
  padding: 5% 10% 0% 10%;
  width:100%;
}

.card {
  position: relative;
  height: 200px;
  width: 22%;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px; /* 5px rounded corners */
  float: left;
  margin: 0% 1.5% 2.5% 1.5%;
}



.cardrow {

}

.container {
  margin:3%;
  height:100%;
  width: 100%;
}

/* Add rounded corners to the top left and the top right corner of the image */
img {
    border-radius: 5px 5px 0 0;
    width:100%;
    height:100%;
    overflow: hidden;
}

#dialog {
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
  position: absolute;
  height: 90%;
  width: 90%;
  margin: -2.5% 5% 0% 5%;
  z-index:5;
  background-color: #deeaee;
}

#close {
  position: absolute;
  float:right;
  display:inline-block;
  padding:2px 5px;
  background:#ccc;
}

#close:hover {
  float:right;
  display:inline-block;
  padding:2px 5px;
  background:#ccc;
  color:#fff;
}

.posterpop {
  float:left;
  margin: 3%;
  border-radius: 5px ;
  width:25%;
  height:80%;
  overflow: hidden;
}

.poster {
  float:left;
  margin: 3%;
  border-radius: 5px ;
  height: 90%;
  width:50%;
  overflow: hidden;
}

.titlepop {

  float:left;
  width: 69%;
}
#titlepop {
  text-align: center;
}

#yearpop {
  float: left;
  width:20%;
}

#genrepop {
  float: left;
  width:22%;
}

#ratedpop {
  float: left;
  width:20%;
}

.pop {
  margin-top: 0px;
  margin-bottom: 2%;
  float:left;
  width: 20%;
}



